  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
  box-sizing: border-box;
  list-style-type: none;
}

html {
  scroll-behavior: smooth;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  background-color: blue;
  color: white;
  z-index: 9999;
  transition: 0.3s;
  font-weight: bold;
  font-size: 0.8rem;
}

.skip-link:focus {
  top: 0;
}

/* header */
.header {
  /* border: 1px solid; */
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 1px 1px 1px 1px #0000002a;
  background-color: #fff;
  z-index: 1000000;
}

.nav {
  /* border: 1px solid; */
  width: 80%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 3.5rem;
}

.nav__logo {
  font-size: 2.3rem;
  font-weight: bold;
  color: black;
  display: inline-block;
  min-height: 44px;
  min-width: 44px;
}

.nav__logo > span {
  color: blue;
}

.nav__menu.open {
  left: 0;
}

.nav__item {
  color: #000;
  padding: 1rem;
}

.nav__item:hover {
  font-weight: bold;
  color: blue;
}

.nav__toggle {
  /* border: 1px solid; */
  outline: none;
  border: none;
  background-color: transparent;
  display: none;
  font-size: 1.5rem;
  cursor: pointer;
}

/* hero */
.hero {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  min-height: 100vh;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  color: #fff;
  align-items: center;
}

.title__content > h1 {
  /* border: 1px solid; */
  text-align: center;
  padding: 3rem;
}

.title__content > h1 > span {
  color: blue;
}

.restaurant-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  padding: 1rem;
}

/* .card {
  border: 1px solid #ccc;
